@mixin fontStyleRobotoLite {
  font-family: Roboto;
  color: #FFFFFF; }

@mixin fontStyleRobotoDark {
  font-family: Roboto;
  color: #2e2e1f; }

.headerTextLite {
  font-size: 2.5em;
  font-weight: bold;
  @include fontStyleRobotoLite; }

.subHeaderTextLite {
  font-size: 1.875em;
  font-family: Roboto, sans-serif;
  color: #FFFFFF;
  @include fontStyleRobotoLite; }

.headerTextDark {
  font-size: 2.5em;
  font-weight: bold;
  @include fontStyleRobotoDark; }

.subHeaderTextDark {
  font-size: 1.875em;
  font-family: Roboto, sans-serif;
  color: #FFFFFF;
  @include fontStyleRobotoDark; }

@media screen and (max-width: 530px) {
  .headerTextLite {
    font-size: 1.875em;
    font-weight: bold;
    @include fontStyleRobotoLite; }

  .subHeaderTextLite {
    font-size: 1.25em;
    @include fontStyleRobotoLite; }

  .headerTextDark {
    font-size: 1.875em;
    font-weight: bold;
    @include fontStyleRobotoDark; }

  .subHeaderTextDark {
    font-size: 1.25em;
    @include fontStyleRobotoDark; } }
